/* HEADER */
.app-header{
    grid-area: app-header;
    background-color: #4e514e;
    position: relative;
}

.logo{
    position: absolute;
    top: 50%;
    left: 10%;
    transform: translate(-10%, -50%);
}


.app-header img {
    width: 50px;
    height: auto;
}

.loginBlock {
    float: right;
    color: white;

}
.loginBlock a {
    color: white;
    text-decoration: none;
}